@import url(https://fonts.googleapis.com/css?family=Encode+Sans+Condensed:100,400,700);

@import url(theme.css);
@import url(components.css);

:not(.no-transition) {
	transition: .2s ease-out;
	transition-property: background, color, opacity, border, box-shadow, transform, max-width, max-height, border-radius;
}

html, body {
	overflow-x: hidden;
	width: 100%;
}

body {
	background: black;
	font-family: "Encode Sans Condensed", sans-serif;
}

.display-1, .display-2, .display-3, .display-4 {
	font-weight: 100 !important;
}

.bg-main {
	background: #FFF url(/assets/bg-main.jpg);
	background-attachment: fixed;
	background-size: cover;
}

.bg-main-inverted {
	background: #050505 url(/assets/bg-main-dark.jpg);
	background-attachment: fixed;
	background-size: cover;
}

.text-thin {
	font-weight: 100;
}

.jumbotron {
	border-radius: 0;
	color: #FFF;
}

.list-group-item {
	background: rgba(255,255,255, .5);
}

.bg-t {
	background: rgba(255,255,255, .9);
	background: linear-gradient(to left, rgba(200,200,200, .9) 0%, rgba(220,220,220, .9) 49%, rgba(235,235,235, .9) 51%, rgba(255,255,255, .9) 100%);
	color: #FFF;
}

.bg-t-dark {
	background: rgba(0,0,0, .2);
	background: linear-gradient(to right, rgba(0,0,0,0.2) 0%, rgba(10,10,10,0.2) 49%, rgba(40,40,40,0.2) 51%, rgba(60,60,60,0.2) 100%);
	color: #FFF;
}

.bg-dark {
	background: #222;
	color: #FFF;
}

.bg-darker {
	background: #111;
	color: #FFF;
}

.bg-darkest {
	background: #000;
	color: #FFF;
}

.h-100 {
	min-height: 100vh;
}

.o-90 {
	opacity: .9;
}

.clickable, .btn, a {
	cursor: pointer;
}

.btn:not(.rounded-circle), input {
	border-radius: 0 !important;
}

.btn:hover {
	opacity: .9;
}

.btn:hover i {
	color: #FFF;
}

::selection {
    background: #444;
    color: #FFF;
}

.noselect {
	cursor: default;
}

.noselect ::selection, .noselect::selection {
	background: inherit;
	color: inherit;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #333;
}
 
::-webkit-scrollbar-thumb {
    background: #555;
}

::-webkit-scrollbar-track:hover {
    background: #444;
}
 
::-webkit-scrollbar-thumb:hover {
    background: #777;
}


