
@keyframes fade-in {
	from {opacity: 0}
	to {opacity: 1}
}

.fade-in {
	animation: fade-in .5s ease-in-out;
}

.hover-reveal {
	transition-duration: .7s !important;
}

.hover-reveal:hover {
	transition-duration: .3s !important;
	border-radius: 30%;
	transform: scale(1.1);
}

.hover-slide:hover > * {
	transform: translateX(10px);
}

.text-wide {
	letter-spacing: 5px;
}

:hover > .btn-mega-tooltip {
	opacity: 1;
}


